<template>
	<div>
		<div class="top">
			<span>数量:</span>
			<span>{{list.length}}</span>
			<div class="delete" @click="deleteAll">
				<div class="icon"></div>
				<span>全部清空</span>
			</div>
		</div>
		<div v-for="item in list" class="item" @click="go(item.id)">
			
			<p v-html="item.title" style="width: 90%;"></p>
			<p>
				<span style="font-size: 0.24rem;color: #999999;">{{item.time}}</span>
			    
			</p>
			
			<i class="go_each"></i>
			
			
		</div>
		
		
		
	
		
	</div>
</template>

<script>
	
	export default{
		
		data(){
			return {
				list:[]
			}
			
		},
		created(){
			this.getData()
			
		},
		methods:{
			go(id){
				this.$router.push({name: 'ErrorItem',params:{errorId:id}})
			},
			deleteAll(){
				this.$popup({
					  title: '提示',
          content: '确认全部清空你的错题集吗?'
				}).then((res)=>{
				
					if(res=='sure'){
                     this.deleteAll()
					}
				})
				
			},
			getData(){
				var obj={
					"s":this.$store.state.sessionId
				}
				this.$Api.ErrorQuestionList(obj).then((res)=>{
					
					this.list=res.q.question
				})
				
			},
			deleteAll(){
				  var obj={
                   	     's':this.$store.state.sessionId,
                   	     'a':2,
                   	     'ids':[]
                   }
				  for(var i=0;i<this.list.length;i++){
				  	 obj.ids.push(this.list[i].id)
				  }
				  
                  
                     that.$Api.DeleteAction(obj).then((res)=>{
                   
                     })
			}
		}
		
	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
.top{
	height: 1rem;
	border-bottom: 0.02rem solid #CCCCCC;
	line-height: 1rem;
	padding-left: 0.3rem;
	font-size: 0.3rem;
	box-sizing: border-box;
	.delete{
		float:right;
		margin-right:0.3rem;
		.icon{
			width: 0.36rem;
			height: 0.38rem;
			background: url(../../../static/study_delete.png) no-repeat;
			background-size:100% 100% ;
			float: left;
			margin-top: 0.3rem;
			margin-right: 0.2rem;
		}
		
	}
}
.item{
	width: 100%;
	
	border-bottom: 0.02rem solid #CCCCCC;
	position: relative;
	font-size: 0.3rem;
	padding: 0.3rem;
	box-sizing: border-box;
	.go_each{
		 width: 0.13rem;
		 height: 0.23rem;
		 background: url(../../assets/go.png) no-repeat;
		 background-size:100% 100% ;
		 position: absolute;
		 right: 0.3rem;
		 top: 40%;
	}
	
}

</style>


